#page-tender {
 width: 100vw;
 height: 100vh;
 
 position: relative;
 display: flex;
}

#page-tender .aside {
  width: 440px;
  padding: 10px;
  max-width: 440px;

  
  background: #25A6E4 url('../../images/pardalHeart.svg') no-repeat 20% bottom;
  background-size: 100% 87%;
 
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

 .image-logo{
  width: 200px;
  height: 100px;
}

#page-tender .back-landing {
  position: absolute;
  right: 40px;
  bottom: 40px;

  width: 64px;
  height: 64px;
  background: #25A6E4;
  border-radius: 20px;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: backgroung-color 0.2s;
}

#page-tender .back-landing:hover {
  background: #2255aa;
}

#page-tender .tenders {
  width: 70vw;
  max-height: 600px;
  overflow-y: scroll;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tenders ul {
  list-style-type: none;
}
.tenders ul li{
  width: 60vw;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;  
}

.tenders li h1 {
  color: #0a90d8;
    font-size: 20px;
  font-weight: bold;
}

.tenders li p {
  color: #25A6E4;
  font-size: 15px;
  text-align: center;
}

.tenders-img {
    width: 80px;
    height: 80px;

    background-color: #25A6E4;
    border-radius: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 3px;

}
.tenders li img {
  width: 50px;
  height: 50px;

  display: flex;
  align-items: center;
  justify-content: center;
}

