#page-landing {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(329.54deg, #25A6E4 0%, #25A6E4 100% );

  display: flex;
  justify-content: center;
  align-items: center;  
}

#page-landing .content-wrapper {
  position: relative;
  
  width: 95vw;
  max-width: 1100px;

  height: 100vh;

  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: space-between;

  background: url('../../images/landing.svg') no-repeat ;
  background-size: auto 90%;
  background-position: right bottom;

}


#page-landing .content-wrapper main h1 {
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 2.5rem;
  font-weight: 980;
  line-height: 5.0rem;
}


 .content-wrapper .partido {
  position: absolute;
  right: 0;
  top: 0;

  font-size: 2.4rem;
  line-height: 2.8rem;

  display: flex;
  flex-direction: column;

  text-align: right;
}

.content-wrapper .partido strong {
  font-weight: 800;
}

.content-wrapper .midias {
  position: relative;
  
  display: flex;
  align-items: center;
  justify-content: center;

  margin-top: 1.0rem;
}

.content-wrapper .enter-midia {
  margin-bottom: 1.0rem;
  margin-left: 1.0rem;

  width: 40px;
  height: 40px;
  background: #FFF;
  border-radius: 14px;

  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s;
}

.content-wrapper .enter-midia:hover {
  background:darkgrey;
}

@media (max-width: 768px){

  #page-landing {
    width: 100%;
    height: 100%;
   
  }
  
  #page-landing .content-wrapper {
    width: 100%;
    height: 100%;
  }

}

@media (max-width: 485px){

  html {
    font-size: 50%;
  }
  

  .content-wrapper .partido {
    margin-top: 1.0rem;
    line-height: 2.0rem;    
    text-align: left;
    padding: 0 auto;
    justify-content: right;
    font-size: 1.3rem;
    order: -1;
    
  }


  .content-wrapper .logo{
    
    justify-content: left;
    width: 40vw;
    
  } 

  .content-wrapper main {
    margin-top: 20rem;
  }

  .midias {
    width: 100vw;
    margin-top: 5px;
    justify-content: center;
    align-items: center;
  }

}