.swiper-container {
  background-color: #fff;
  width: 95vw;
  max-width: 600px;
  height: 50vh;
  box-shadow: 4px 4px 5px rgba(0,0,0,0.2);
  border: 1px solid #ddd;
  border-radius: 1.0rem;
}

.slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide-content {
  background-color:#FFF;
  width: 100vw;
  height: 50vh;
  max-width: 500px;
  margin: auto;
  border-radius: 1.0em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 3.5rem;

  
}

.user-image {
  width: 8.0rem;
  height: 8.0rem;
  background-color: #25A6E4;
  border-radius: 4.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  margin-bottom: 2.0rem;
}

.user-photo {
  width: 5.0rem;
  height: 5.0rem;
}

.user-description {
  font-size: 1.3rem;
  color:#25A6E4;
}

.slide-content h3 {
  color: #0a90d8;
  font-size: 2.0rem;
  font-weight: bold;
  margin-bottom: 1.0rem;
}



@media (max-width: 485px){
  .swiper-container {
    height: 40vh;
    
  }

  .slide-content {
    height: 43vh;
    padding: 4.5rem;
  }

  .user-description {
    font-size: 1.0rem;
    color:#25A6E4;
  }
  
  .slide-content h3 {
    color: #0a90d8;
    font-size: 1.5rem;
    font-weight: bold;
  }
}
